___CSS_LOADER_EXPORT___.push([module.id, `*{
    padding: 0;
    margin: 0;
}
body{
    background-color: rgb(57, 16, 96);
}
.container{
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: white;
    margin: auto;
    margin-top: 20px;
    width: 450px;
    border-radius: 10px;
    box-shadow: 2px 2px 3px black;
}
#operacoes{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.inputs{
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 5px;
    margin: 2px;
    border-radius: 5px;
    box-shadow: 2px 2px 3px black;
}
button{
    padding: 5px;
    margin: 10px;
    width: 200px;
    border: 1px solid blueviolet;
    background-color: rgba(137, 43, 226, 0.337);
    border-radius: 5px;
    cursor: pointer;
}
button:hover{
    background-color: white;
    transition: .2s;
}
#quantidade{
    width: 50px;
}
#res{
    color: blueviolet;
    font-weight: bold;
}`, "",{"version":3,"sources":["webpack://./src/assets/css/style.css"],"names":[],"mappings":"AAAA;IACI,UAAU;IACV,SAAS;AACb;AACA;IACI,iCAAiC;AACrC;AACA;IACI,aAAa;IACb,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,sBAAsB;IACtB,uBAAuB;IACvB,YAAY;IACZ,gBAAgB;IAChB,YAAY;IACZ,mBAAmB;IACnB,6BAA6B;AACjC;AACA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,sBAAsB;AAC1B;AACA;IACI,WAAW;IACX,aAAa;IACb,8BAA8B;IAC9B,YAAY;IACZ,WAAW;IACX,kBAAkB;IAClB,6BAA6B;AACjC;AACA;IACI,YAAY;IACZ,YAAY;IACZ,YAAY;IACZ,4BAA4B;IAC5B,2CAA2C;IAC3C,kBAAkB;IAClB,eAAe;AACnB;AACA;IACI,uBAAuB;IACvB,eAAe;AACnB;AACA;IACI,WAAW;AACf;AACA;IACI,iBAAiB;IACjB,iBAAiB;AACrB","sourcesContent":["*{\r\n    padding: 0;\r\n    margin: 0;\r\n}\r\nbody{\r\n    background-color: rgb(57, 16, 96);\r\n}\r\n.container{\r\n    padding: 10px;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    flex-direction: column;\r\n    background-color: white;\r\n    margin: auto;\r\n    margin-top: 20px;\r\n    width: 450px;\r\n    border-radius: 10px;\r\n    box-shadow: 2px 2px 3px black;\r\n}\r\n#operacoes{\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    flex-direction: column;\r\n}\r\n.inputs{\r\n    width: 100%;\r\n    display: flex;\r\n    justify-content: space-between;\r\n    padding: 5px;\r\n    margin: 2px;\r\n    border-radius: 5px;\r\n    box-shadow: 2px 2px 3px black;\r\n}\r\nbutton{\r\n    padding: 5px;\r\n    margin: 10px;\r\n    width: 200px;\r\n    border: 1px solid blueviolet;\r\n    background-color: rgba(137, 43, 226, 0.337);\r\n    border-radius: 5px;\r\n    cursor: pointer;\r\n}\r\nbutton:hover{\r\n    background-color: white;\r\n    transition: .2s;\r\n}\r\n#quantidade{\r\n    width: 50px;\r\n}\r\n#res{\r\n    color: blueviolet;\r\n    font-weight: bold;\r\n}"],"sourceRoot":""}]);
